<template>
  <div class="box">
    <Loading v-if="!proDetailData.type" />
    <div class="pro_box" v-else>
      <div class="pro_pic">
        <img :src="proDetailData.pro_pic" alt="">
        <a @click="goBack">
          <van-icon name="arrow-left" />
        </a>
      </div>
      <div class="pro_pro">
        <p class="pro_price">
          <b>￥</b>
          <span>{{proDetailData.pro_price}}</span>
        </p>
        <p class="pro_introduce">{{proDetailData.pro_introduce}}</p>
        <p class="pro_city">
          <span>{{proDetailData.pro_city}}</span>
          <span>{{'已预约' + proDetailData.subscribe}}</span>
        </p>
        <p class="pro_welfare">
          优惠
          <b>红包</b>
          <span @click="changeWelfareSheetShow">{{proDetailData.pro_discounts}}</span>
        </p>
        <div class="clinic">
          <img :src="proDetailData.clinic_pic" alt="">
          <div class="clinic_text">
            <p>{{proDetailData.clinic_name}}</p>
            <span>{{'地址：' + proDetailData.clinic_address}}</span>
            <div class="clinic_serviceName">
              <div>
                <van-icon name="phone-o" />
                <b @click="changeShowBool">电话咨询</b>
              </div>
              <span>|</span>
              <div>
                <van-icon name="chat-o" />
                <b @click="serve('100004')">在线客服</b>
              </div>
            </div>
          </div>
        </div>
      </div>
      <van-popup v-model="copyTelShow" round>
        <div class="copyTel" data-clipboard-target=".copyTel" @click="copy">
          <span>{{proDetailData.clinic_tel}}</span>
          <span>复制</span>
        </div>
      </van-popup>
      <recommend />
      <div class="pro_buy">
        <div>
          <span>{{'￥' + proDetailData.pro_price}}</span>
          <span @click="goOrder(proDetailData.proid)">立即购买</span>
        </div>
        <img @click="chat" src="@/assets/images/icon_kefu.png" alt="">
      </div>
      <van-action-sheet class="welfare_sheet" v-model="show" title="优惠劵">
        <div v-if="myCouponsList.length === 0" class="welfare_sheet_box">
          <h4 class="welfare_sheet_box_title">可使用的优惠卷</h4>
          <div class="welfare_sheet_box_text">您还没有领取优惠卷，快去<span @click="getCoupons">领取</span>吧！</div>
        </div>
        <div v-else class="welfare_sheet_box">
          <h4 class="welfare_sheet_box_title">可使用的优惠卷</h4>
          <div class="welfare_sheet_box_pic" @click="selectCoupon" :id="getCouponBool ? 'welfare_sheet_box_pic' : 'welfare_sheet_box_pic_select'">
            <div class="welfare_sheet_box_pic_left">
              <p>{{myCouponsList[0].denomination}}</p>
              <p>{{myCouponsList[0].money_off}}</p>
              <p>满减红包</p>
            </div>
            <div class="welfare_sheet_box_pic_right">
              <p>不退款验证赠送红包</p>
              <p>{{myCouponsList[0].coupons_type}}</p>
              <p>{{myCouponsList[0].due_time}}</p>
            </div>
          </div>
        </div>
      </van-action-sheet>
    </div>
  </div>
</template>

<script>
import Loading from '../../components/loading'
import { getWelfareProDetailList, queryMyCoupons } from '../../api'
import Recommend from './recommend.vue'
import Clipboard from 'clipboard'
import Vue from 'vue'
import { Icon, ActionSheet, Toast, Popup } from 'vant'
Vue.use(Icon)
Vue.use(ActionSheet)
Vue.use(Toast)
Vue.use(Popup)
export default {
  data () {
    return {
      copyTelShow: false,
      show: false,
      proid: '',
      proDetailData: [],
      preferentialPrice: 0,
      welfareBool: true,
      myCouponsList: [],
      pro_discounts: '',
      getCouponBool: true,
      loginState: localStorage.getItem('loginState_AM')
    }
  },
  components: {
    Recommend,
    Loading
  },
  mounted () {
    this.proid = this.$route.query.proid
    this.getProDetail(this.proid)
  },
  methods: {
    serve (id) {
      if (this.loginState === 'no' || this.loginState === null) {
        Toast('您还没有登录，快去登录吧')
        this.$router.push('/login')
      } else {
        this.$router.push({ path: '/communication/' + 'servicerid=' + id })
      }
    },
    changeShowBool () {
      this.copyTelShow = true
    },
    copy () {
      var clipboard = new Clipboard('.copyTel')
      clipboard.on('success', e => {
        // console.log('复制成功', e)
        Toast({
          message: '复制成功'
        })
        // 释放内存
        clipboard.destroy()
      })
      clipboard.on('error', e => {
        // 不支持复制
        Toast({
          message: '手机权限不支持复制功能'
        })
        // console.log('该浏览器不支持自动复制')
        // 释放内存
        clipboard.destroy()
      })
    },
    // 官方客服
    chat () {
      window._MEIQIA('withoutBtn')
      window._MEIQIA('init')
      window._MEIQIA('allSet', function () {
        window._MEIQIA('showPanel')
      })
    },
    selectCoupon () {
      this.getCouponBool = false
      this.preferentialPrice = this.myCouponsList[0].denomination.substring(1)
    },
    goBack () {
      this.$router.go(-1)
    },
    // collectPro () {
    //   // console.log('收藏')
    // },
    getProDetail (proid) {
      getWelfareProDetailList({ proid: proid }).then(res => {
        this.proDetailData = res.data.data[0]
        this.pro_discounts = this.proDetailData.pro_discounts
      })
    },
    goOrder (id) {
      if (this.loginState === 'no' || this.loginState === null) {
        Toast('您还没有登录，快去登录吧')
        this.$router.push('/login')
      } else {
        const proid = id.toString()
        this.$router.push({ path: '/order', query: { proid: proid, preferentialPrice: this.preferentialPrice } })
      }
    },
    changeWelfareSheetShow () {
      if (this.loginState === 'no' || this.loginState === null) {
        Toast('您还没有登录，快去登录吧')
        setTimeout(() => {
          this.$router.push('/login')
        }, 300)
      } else {
        this.show = true
        queryMyCoupons({
          username: localStorage.getItem('userTel_AM'),
          token: localStorage.getItem('token_AM')
        }).then(res => {
          if (res.data.code === '200') {
            const myCouponsList = []
            res.data.data.map(item => {
              if (this.pro_discounts.indexOf(item.money_off) === 5) {
                myCouponsList.push(item)
              }
            })
            this.myCouponsList = myCouponsList
          }
        })
      }
    },
    getCoupons () {
      this.$router.replace('/welfare')
    }
  }
}
</script>

